// Импортируем глобальные переменные
@import 'master';

@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Open Sans Regular'), local('OpenSans-Regular'),
    url('../fonts/OpenSans-Regular.ttf') format('woff2');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local('Open Sans Bold'), local('OpenSans-Bold'),
    url('../fonts/OpenSans-Bold.ttf') format('woff2');
}

@font-face {
  font-family: 'Segoe Print';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Segoe Print'), local('Segoe-Print'),
    url('../fonts/Segoe_Print.ttf') format('woff2');
}

html,
body {
  height: 100%;
  margin: 0;
  color: $text;
  font-family: 'Open Sans', sans-serif;
  font-size: 16px;
}
body {
  display: flex;
  flex-direction: column;
}

section {
  padding: 40px 0;
}

.segoe-font {
  font-family: 'Segoe Print', sans-serif;
}

.hamburger {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
  .bar1,
  .bar2,
  .bar3 {
    width: 35px;
    height: 5px;
    background-color: #ffffff;
    margin: 6px 0;
    transition: 0.4s;
  }

  &.active .bar1 {
    -webkit-transform: rotate(-45deg) translate(-9px, 6px);
    transform: rotate(-45deg) translate(-8px, 6px);
  }

  &.active .bar2 {
    opacity: 0;
  }

  &.active .bar3 {
    transform: rotate(45deg) translate(-9px, -8px);
  }
}

.neon-overlay {
  position: relative;
  &::after {
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    background-color: $main;
    opacity: 0.16;
    width: 100%;
    height: 100%;
  }
}

.filling-button {
  padding: 6px 12px;
  background: transparent;
  border: none;
  position: relative;
  transition: all 600ms;
  &::after {
    position: absolute;
    content: '';
    width: 100%;
    height: 2px;
    left: 0;
    bottom: 0;
    background-color: $main;
    border-radius: 8px;
    z-index: -1;
    transition: all 600ms;
  }
  &:hover {
    color: #fff;
    &::after {
      width: 100%;
      height: 100%;
      left: 0;
      bottom: 0;
    }
  }
}

.slick-arrow {
  &::before {
    color: #000;
  }
}
.slick-next {
  z-index: 1;
  right: 0;
}
.slick-prev {
  z-index: 1;
  left: 0;
}

.branding {
  .logo {
    width: 210px;
  }
}

.section-title {
  font-size: 1.8rem;
  font-weight: bold;
}
.section-brand-title {
  color: $main;
  font-size: 3.5rem;
  font-weight: bold;
}

.header {
  .header-top-content {
    background-color: $secondary;
    .header-top-content-inner {
      height: 210px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .social {
        .social-logo {
          &:not(:last-child) {
            margin-right: 40px;
          }
          &:hover {
            opacity: 0.8;
          }
        }
      }
      .contact-phone {
        color: #fff;
        font-weight: bold;
        font-size: 1.75rem; // 28px for 16px rem
      }
    }
  }
  .header-bottom-content {
    background: #f2f2f2;
    .header-bottom-content-inner {
      height: 120px;
      .nav-bar {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        .nav-menu {
          border: 1px solid $main;
          padding: 12px 0;
          width: 100%;
          border-radius: 25px;
          box-shadow: 0 5px 10px 0 rgba(238, 14, 81, 0.25);
          list-style: none;
          margin: 0;
          padding: 15px 0;
          display: flex;
          justify-content: center;
          align-items: center;
          .nav-menu-item {
            .nav-menu-link {
              color: $text;
              font-weight: bold;
              font-size: 1.25rem; // 20px for 16px rem
              transition: 200ms;
              &:hover {
                color: $main;
                text-decoration: none;
                text-shadow: 0 5px 10px rgba(238, 14, 81, 0.25);
              }
            }
            &:not(:last-child) {
              margin-right: 50px;
            }
          }
        }
      }
    }
  }
  .header-fixed {
    display: none;
    &.active {
      display: block;
    }
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 888;
    background: $secondary;
    color: #fff;
    .header-fixed-inner {
      display: flex;
      justify-content: space-between;
      align-items: center;
      min-height: 120px;

      .social {
        .social-logo {
          &:not(:last-child) {
            margin-right: 20px;
          }
          &:hover {
            opacity: 0.8;
          }
        }
      }
      .branding {
        .logo {
          width: 90px;
        }
      }
      .nav-bar {
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        .nav-menu {
          padding: 12px 0;
          width: 100%;
          list-style: none;
          margin: 0;
          padding: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          .nav-menu-item {
            .nav-menu-link {
              color: #fff;
              font-weight: bold;
              font-size: 1rem; // 16px for 16px rem
              transition: 200ms;
              &:hover {
                color: $main;
                text-decoration: none;
                text-shadow: 0 5px 10px rgba(238, 14, 81, 0.25);
              }
            }
            &:not(:last-child) {
              margin-right: 25px;
            }
          }
        }
      }
      .contact-phone {
        color: #fff;
        font-weight: bold;
        font-size: 1.25rem; // 28px for 16px rem
      }
    }
  }
}
.content {
  flex: 1 0 auto;
  .landing-section {
    background-image: url('../images/landing_background.png');
    background-repeat: no-repeat;
    background-size: cover;
    height: 745px;

    background-attachment: fixed;
    background-position: left;

    display: flex;
    justify-content: center;
    align-items: center;

    .landing-content {
      z-index: 10;
      background: rgba(242, 242, 242, 0.6);
      border-radius: 25px;
      padding: 50px 60px;
      margin: 10px;
      .landing-title {
        text-align: center;
        margin-bottom: 35px;
      }
      .landing-button-wrapper {
        text-align: center;
      }
    }
  }

  .about-block {
    .about-title {
      font-size: 1.5rem;
    }
    .about-brand-title {
      font-size: 2.5rem;
      color: $main;
    }
    .about-content {
      padding: 15px 20px;
      border: 2px solid $main;
      border-radius: 12px;
    }
  }

  .projects-carousel-section {
    .project-item {
      position: relative;
      cursor: pointer;
      &:hover {
        .project-item-overlay {
          opacity: 1;
        }
      }
      .project-item-overlay {
        transition: 400ms;
        display: flex;
        opacity: 0;
        justify-content: center;
        align-items: center;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba($main, 0.16);
        p {
          color: #fff;
        }

        &::before,
        &::after {
          content: '';
          position: absolute;
          width: 35px;
          height: 35px;
          border-radius: 50%;
          background: #fff;
          border: 12px solid $main;
        }
        &::before {
          top: 14px;
          left: 14px;
        }
        &::after {
          bottom: 14px;
          right: 14px;
        }
      }
      img {
        width: 100%;
      }
    }
  }

  .tabs {
    .tabs-header {
      display: flex;
      justify-content: space-between;
      .tab-control {
        text-align: center;
        width: 100%;
        padding: 10px 8px;
        .tab-control-button {
          position: relative;
          width: 100%;
          background: none;
          height: 100%;
          outline: none;
          border: none;
          .img-wrapper {
            margin-bottom: 8px;
            &::after {
              transition: 400ms;
              opacity: 0;
              border-radius: 25px;
            }
            img {
              border-radius: 25px;
              width: 100%;
            }
          }
          p {
            min-height: 38px;
          }
          &::after {
            position: absolute;
            left: 0;
            bottom: 0;
            content: '';
            height: 2px;
            background: $main;
            width: 0;
            transition: 400ms;
          }
          &.active {
            .img-wrapper {
              &::after {
                opacity: 0.16;
              }
            }
            &::after {
              width: 100%;
            }
          }
        }
      }
    }
    .tabs-body {
      padding: 10px 15px;
      .tab-content {
        display: none;
        &.active {
          display: block;
        }
      }
    }
  }

  .package {
    cursor: pointer;
    margin-bottom: 10px;
    &::after {
      border-radius: 25px;
      opacity: 0;
      transition: 400ms;
    }
    &:hover {
      &::after {
        opacity: 0.16;
      }
    }
    .package-top {
      position: relative;
      img {
        width: 100%;
        border-radius: 25px;
      }
      .arrow-up {
        width: 0;
        height: 0;
        border-left: 8px solid transparent;
        border-right: 8px solid transparent;

        border-bottom: 8px solid #fff;

        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
      }
    }
    .package-bottom {
      padding: 20px 15px;
      text-align: center;
      .package-name {
        text-transform: uppercase;
        font-size: 1.2rem;
        margin-bottom: 15px;
      }
      .package-size {
        display: inline-block;
        font-size: 1.8rem;
        margin-bottom: 20px;
        border-bottom: 2px solid $main;
      }
    }
  }

  .features-line-section {
    margin: 100px 0;
    .label {
      display: inline-block;
      border: 2px solid $main;
      background: rgba(255, 255, 255, 0.35);
      padding: 15px 25px;
      text-transform: uppercase;
      border-radius: 25px;
      font-weight: bold;
      a {
        color: inherit;
        text-decoration: none;
      }
      &:hover {
        opacity: 0.8;
      }
    }
    .features-top {
      background: $secondary;
      .left {
        position: relative;
        .img-wrapper {
          position: relative;
          margin-top: -100px;
          &::after {
            border-radius: 25px;
          }
          img {
            width: 100%;
            border-radius: 25px;
          }
          .label {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            right: -140px;
            z-index: 100;
            color: #fff;
            white-space: nowrap;
          }
        }
        .features-text {
          margin-bottom: -100px;
          .features-title {
            font-size: 8rem;
            color: $main;
          }
          .features-sub-title {
            font-size: 1.8rem;
            color: $main;
            margin-top: -50px;
            margin-left: 160px;
          }
        }
      }
      .right {
        .img-row {
          margin-bottom: -100px;
          display: flex;
          justify-content: flex-end;
          .img-wrapper {
            &::after {
              border-radius: 25px;
            }
            img {
              width: 100%;
              border-radius: 25px;
            }
          }
        }
      }
    }
    .features-bottom {
      margin-top: 40px;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
    }
  }

  .why-us-cards {
    display: flex;
    width: 100%;
    flex-wrap: wrap;
    .why-us-card {
      flex: 1 1 33%;
      width: 100%;
      padding: 0 5px;
      margin-bottom: 10px;
      .card-top {
        position: relative;
        .img-wrapper {
          &::after {
            border-radius: 25px;
          }
          img {
            width: 100%;
            border-radius: 25px;
          }
        }
        .arrow-up {
          width: 0;
          height: 0;
          border-left: 8px solid transparent;
          border-right: 8px solid transparent;

          border-bottom: 8px solid #fff;

          position: absolute;
          bottom: 0;
          left: 50%;
          transform: translateX(-50%);
        }
      }
      .card-bottom {
        padding: 20px 15px;
        text-align: center;

        box-shadow: 0 3px 6px 0px rgba(0, 0, 0, 0.16);
        border-radius: 25px;
        .card-name {
          text-transform: uppercase;
          font-size: 1.2rem;
          margin-bottom: 15px;
          min-height: 60px;
        }
        .card-link {
          display: inline-block;
          color: $text;
          font-size: 1.4rem;
          margin-bottom: 20px;
          // border-bottom: 2px solid $main;
          position: relative;
          font-weight: bold;
          &::after {
            position: absolute;
            left: 0;
            bottom: -5px;
            content: '';
            height: 2px;
            background: $main;
            width: 0;
            transition: 400ms;
          }
          &:hover {
            &::after {
              width: 100%;
            }
          }
        }
      }
    }
  }

  .why-us-title {
    font-size: 2.5rem;
    margin-bottom: 12px;
  }
  .why-us-list {
    list-style: none;
    margin: 0;
    padding: 0;
    font-size: 1.8rem;
    .list-number {
      color: $main;
    }
    li {
      text-transform: uppercase;
      margin-bottom: 10px;
    }
  }
}
.footer {
  flex-shrink: 0;
  background: $secondary;
  color: #fff;
  padding: 40px 0;

  #backToTop {
    color: $main;
    text-decoration: none;
    .arrow-up {
      width: 60px;
    }
  }
}

.fixed-phone {
  position: fixed;
  z-index: 10;
  bottom: 20px;
  right: 20px;

  .float-up-btns {
    flex-direction: column;
    display: none;
    &.active {
      display: flex;
    }
    .fixed-phone-btn {
      margin-bottom: 10px;
      position: relative;
      .floating-phone-text {
        position: absolute;
        border: 1px solid $main;
        border-radius: 25px;
        background: rgba(255, 255, 255, 0.6);
        padding: 6px 15px;
        min-width: 135px;
        left: -135px;
        font-size: 0.625rem; // 10px for 16px rem
        top: 8px;
      }
    }
  }
  .fixed-phone-btn {
    background: transparent;
    border: none;
    outline: none;
    transition: all 500ms;
    &:hover {
      opacity: 0.8;
    }
  }
}
